import { React, useCallback, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Footer from '../footer';
import Header from '../header';
import MakerEditor from './makerEditor';
import MakerPreview from './makerPreview';

const Maker = ({ FileInput, authService, cardRepository }) => {
  //     object  key     
  // const [cards, setCards] = useState(
  //   {
  //     1: {
  //       id: '1',
  //       name: '',
  //       company: 'Hivelab',
  //       title: 'Web Developer',
  //       email: 'email@email.com',
  //       message: 'Not all those who wander are lost.',
  //       theme: 'Dark',
  //       fileName: null,
  //       fileURL: null
  //     },
  //     2: {
  //       id: '2',
  //       name: '',
  //       company: 'NINIZ',
  //       title: 'Freeter',
  //       email: 'jordi@email.com',
  //       message: ' ',
  //       theme: 'Jordi',
  //       fileName: null,
  //       fileURL: null
  //     },
  //     3: {
  //       id: '3',
  //       name: '',
  //       company: 'Nagano\'s Market',
  //       title: 'Kuma',
  //       email: 'kuma@email.com',
  //       message: '',
  //       theme: 'Light',
  //       fileName: null,
  //       fileURL: null
  //     }
  //   }
  // )


  const navigate = useNavigate();
  const [cards, setCards] = useState({});
  //  ID
  const historyState = navigate?.location?.state;
  const [userId, setUserId] = useState(historyState && historyState.id);

  const onLogout = useCallback(() => {
    authService.logout();
  }, [authService]);

  // 
  useEffect(() => {
    authService
      .onAuthChange(user => {
        if (user) {
          setUserId(user.uid);
        } else {
          navigate('/');
        }
      })
  }, [authService, userId, navigate])

  // DB
  useEffect(() => {
    if (!userId) return;
    const stopSync = cardRepository.syncCard(userId, cards => {
      setCards(cards);
    })
    return () => stopSync();
  }, [userId, cardRepository]);

  const createOrUpdateCard = (card) => {
    //   
    // const updated = {...cards};
    // updated[card.id] = card;
    // setCards(updated);

    // : 
    // state   .         
    // setCards   (cards)   card key  
    //   object     => addCard    !
    setCards(cards => {
      const updated = {...cards};
      updated[card.id] = card;
      return updated;
    });

    // DB 
    cardRepository.saveCard(userId, card);
  }

  const deleteCard = (card) => {
    setCards(cards => {
      const updated = {...cards};
      delete updated[card.id];
      return updated;
    });

    // DB 
    cardRepository.removeCard(userId, card);
  }

  return (
    <section className="maker-wrap">
      <Header onLogout={onLogout} />
      <div className="maker-content">
        <MakerEditor 
          FileInput={FileInput} 
          cards={cards} 
          addCard={createOrUpdateCard} 
          updateCard={createOrUpdateCard} 
          deleteCard={deleteCard}
        />
        <MakerPreview cards={cards} />
      </div>
      <Footer />
    </section>
  )
};

export default Maker;